[class^='level'] {
  display: none;
}

.level0 {
  display: block;
}

[class^='level'] {
  position: relative;
  width: 100%;
  height: 100%;
}

[class^='level'] [class^='block'] {
  position: absolute;
  background-color: #ccc;
}

[class^='level'] [class^='block']::after {
  content: '';
  position: absolute;
  left: -10px;
  right: -10px;
  top: -10px;
  bottom: -10px;
  border-radius: 11px;
}

.setting-input:checked ~ .game [class^='level'] [class^='block']::after {
  border: 1px solid red;
}

[class^='point'] {
  position: absolute;
  top: 100px;
  left: 100px;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background: violet;
  transition: all 0.3s ease-out;
  cursor: url("../images/circle-dark-gray.png") 10 10, crosshair;
}

[class^='point']::after {
  content: '';
  position: absolute;
  left: -1px;
  right: -1px;
  bottom: -1px;
  top: -1px;
  background-color: #fff;
  transform: scale(0);
  border-radius: 50%;
  transition: all 0.3s ease-out;
  transform-origin: center;
  cursor: url("../images/circle-gray.png") 10 10, crosshair;
}

@keyframes appear {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes disappear {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

.welcome {
  display: block;
  width: 100vw;
  height: 100vh;
  min-width: 1300px;
  min-height: 800px;
  background-color: #fff;
  position: absolute;
  left: calc((1000px - 100vw) / 2);
  left: 0;
  top: 0;
  z-index: 999;
  transition: all 0.4s ease-out;
}

input[name='welcome'][value='1']:checked ~ .welcome {
  background-color: transparent;
  pointer-events: none;
}

.welcome .welcome-title {
  font-size: 150px;
  position: absolute;
  top: 300px;
  left: 50%;
  transform: translate(-50%, -50%);
  font-weight: 400;
  transition: all 0.3s ease-out;
  color: #333;
  animation-name: appear;
  animation-duration: 1.2s;
  animation-iteration-count: 1;
  animation-fill-mode: backwards;
  animation-timing-function: ease-in;
}

input[name='welcome'][value='1']:checked ~ .welcome .welcome-title {
  transform-origin: 0 0;
  transform: translate(-592px, -289px) scale(0.24);
  animation-name: disappear;
  animation-duration: 0.01s;
  animation-delay: 0.3s;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
}

.welcome label.start {
  position: absolute;
  top: 650px;
  left: 50%;
  transform: translateX(calc(-50% + 150px));
  color: #333;
  text-transform: uppercase;
  text-decoration: none;
  font-size: 32px;
  font-weight: 700;
  cursor: url("../images/circle-dark-gray.png") 10 10, crosshair;
  transition: all 0.3s ease-out;
  animation-name: appear;
  animation-duration: 1.2s;
  animation-iteration-count: 1;
  animation-fill-mode: backwards;
  animation-timing-function: ease-in;
}

.game:hover ~ .welcome label.start {
  opacity: 0;
  visibility: hidden;
}

input[name='welcome'][value='1']:checked ~ .welcome label.start {
  color: #999;
  opacity: 0;
  visibility: hidden;
}

.rules {
  position: absolute;
  top: 650px;
  left: 50%;
  transform: translateX(calc(-50% - 150px));
  color: #333;
  text-transform: uppercase;
  text-decoration: none;
  font-size: 32px;
  font-weight: 700;
  cursor: url("../images/circle-dark-gray.png") 10 10, crosshair;
  transition: all 0.3s ease-out;
}

.welcome .rules {
  animation-name: appear;
  animation-duration: 1.2s;
  animation-iteration-count: 1;
  animation-fill-mode: backwards;
  animation-timing-function: ease-in;
}

.game:hover ~ .rules {
  opacity: 0;
  visibility: hidden;
}

input[name='welcome'][value='1']:checked ~ .welcome .rules {
  color: #999;
  opacity: 0;
  visibility: hidden;
}

.rules .rules-list {
  display: none;
  position: absolute;
  top: -100%;
  left: 50%;
  width: 300px;
  transform: translate(-50%, -100px);
  padding: 10px 20px;
  box-shadow: 1px 1px 5px #999;
  list-style: none;
  text-transform: none;
}

.rules .rules-list .rule {
  font-size: 18px;
}

.rules:hover .rules-list {
  display: block;
}

.gameover {
  display: none;
  background-color: white;
  position: absolute;
  left: -310px;
  right: -10px;
  top: 0;
  bottom: -300px;
  z-index: 1000;
}

.gameover:hover {
  display: block;
}

.setting-input:not(:checked) ~ .game [class^='block']:hover ~ .gameover {
  display: block;
}

.gameover .restart {
  position: absolute;
  top: 650px;
  left: 50%;
  transform: translateX(-50%);
  color: #333;
  text-transform: uppercase;
  text-decoration: none;
  font-size: 32px;
  font-weight: 700;
  cursor: url("../images/circle-dark-gray.png") 10 10, crosshair;
}

.gameover .title {
  position: absolute;
  font-size: 150px;
  text-align: center;
  width: 100%;
  top: 300px;
  transform: translateY(-50%);
  text-transform: uppercase;
  font-weight: 700;
  color: #333;
}

.completed {
  display: none;
  background-color: white;
  position: absolute;
  left: -310px;
  right: -10px;
  top: 0;
  bottom: -300px;
  z-index: 1000;
}

.completed .title {
  font-size: 70px;
  text-align: center;
  position: absolute;
  width: 100%;
  top: 300px;
  transform: translateY(-50%);
  text-transform: uppercase;
  font-weight: 700;
  color: #333;
}

.completed [class^='result'] {
  position: absolute;
  top: 650px;
  left: 50%;
  transform: translateX(calc(-50% + 150px));
  color: #333;
  text-transform: uppercase;
  text-decoration: none;
  font-size: 32px;
  font-weight: 700;
  cursor: url("../images/circle-dark-gray.png") 10 10, crosshair;
}

.completed .restart {
  position: absolute;
  top: 650px;
  left: 50%;
  transform: translateX(-50%);
  color: #333;
  text-transform: uppercase;
  text-decoration: none;
  font-size: 32px;
  font-weight: 700;
  cursor: url("../images/circle-dark-gray.png") 10 10, crosshair;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

html {
  cursor: url("../images/circle-gray.png") 10 10, crosshair;
}

body {
  font-family: 'Roboto', sans-serif;
  min-height: 800px;
}

input {
  display: none;
}

@keyframes bounce {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(-10px);
  }
}

.container {
  display: flex;
  justify-content: center;
  min-width: 1300px;
}

.left-column {
  border-left: 1px solid #999;
  width: 300px;
  flex-shrink: 0;
}

.game {
  position: relative;
  width: 1000px;
  height: 600px;
  flex-shrink: 0;
}

.game::after {
  content: '';
  position: absolute;
  border: 30px solid transparent;
  border-bottom-color: #999;
  left: calc(50% - 30px);
  top: calc(100% - 20px);
  pointer-events: none;
  transition: all 0.3s;
  animation-name: bounce;
  animation-duration: 0.4s;
  animation-iteration-count: infinite;
  animation-timing-function: ease-in-out;
  animation-direction: alternate;
}

.game:hover::after {
  border-bottom-color: transparent;
}

.settings-label {
  cursor: url("../images/circle-dark-gray.png") 10 10, crosshair;
}

.game-title {
  font-size: 36px;
  text-align: center;
  margin-top: 10px;
  margin-bottom: 10px;
  color: #333;
  flex-shrink: 0;
}
